.breadcrumbs {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: rem(36);

	@include min540 {
		margin-bottom: rem(20);
	}

	a {
		position: relative;
		font-size: rem(12);
		margin-right: rem(28);

		&:hover {
			text-decoration: underline;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			right: rem(-16);
			pointer-events: none;
		}
	}

	span {
		color: var(--c-black-3);
	}
}