.slider {
	height: rem(325);
	position: relative;
	margin-bottom: rem(50);
	
	@include min {
		margin-bottom: rem(30);
	}
	
	@include min540 {
		height: rem(280);
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		height: 100%;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0,0,0,.2);
			z-index: -1;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			z-index: -2;
		}

		.title {
			font-weight: 600;
			font-size: rem(18);
			line-height: rem(24);
			color: white;
			text-transform: uppercase;
			max-width: rem(300);
			margin-left: rem(100);

			@include min540 {
				max-width: rem(220);
				margin-left: rem(60);
			}
		}
	}

	.swiper-button-prev,
	.swiper-button-next {
		width: rem(35);
		height: rem(35);
		border: rem(3) solid white;
		border-radius: 50%;

		i {
			font-size: rem(18);
		}
	}
}

.brand__pagination {
	position: absolute;
	bottom: rem(20);
	left: 0;
	z-index: 10;

	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	li {
		margin-right: rem(20);
		margin-bottom: rem(16);
	}

	button, li > a {
		padding: 0;
		margin-right: rem(20);

		transition: all 300ms ease;

		padding-bottom: rem(8);
		border-bottom: rem(1) solid transparent;


		img {
			transition: all 300ms ease;
			// opacity: .2;
		}

		&:hover {
			border-bottom: rem(1) solid var(--c-yellow);

			img {

				opacity: 1;
			}
		}

		&:last-child {
			margin-right: 0;
		}
	}

	button.active,
	li>a.active {

		border-bottom: rem(1) solid var(--c-yellow);

		img {
			
			opacity: 1;
		}
	}
}
.brand__pagination.default {
	position: relative;
	bottom: auto;
	left: auto;
	justify-content: flex-start;
	margin-bottom: rem(38);

	@include min540 {
		margin-bottom: rem(20);
	}

	a {
		img {
			opacity: .2;
		}

		&:hover {
			img {

				opacity: 1;
			}
		}
	}

	a.active {

		img {

			opacity: 1;
		}
	}
}