.article {
	
	&__top {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(38);

		@include min540 {
			margin-bottom: rem(30);
		}

		&-image {
			height: rem(352);
			border: rem(1) solid var(--c-gray);
			overflow: hidden;

			@include min540 {
				height: rem(280);
				margin-bottom: rem(20);
			}
		}

		&-list {
			// width: 100%;
			margin-bottom: rem(50);

			@include min540 {
				margin-bottom: rem(30);
			}

			li {
				padding: rem(14) rem(20);
				background-color: var(--c-gray5);
				display: flex;
				flex-wrap: wrap;

				@include min360 {
					font-size: rem(12);
				}

				span {
					// &:first-child {
					// 	width: rem(214);
					// }
					// &:last-child {
					// 	flex-grow: 1;
					// }
					width: 50%;
				}

				&:nth-child(2n) {
					background-color: white;
				}
			}
		}

		&-control {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			margin-bottom: rem(40);
			

			.price {
				font-size: rem(22);
				line-height: rem(24);
				font-weight: 500;
			}

			@include min {
				margin-bottom: rem(20);
				.price {
					flex: 0 0 70%;
				}
				.field_quantity {
					flex: 0 0 10%;
				}
				.button_box {
					margin-top: rem(24);
				}
			}

			@include min540 {
				.price {
					flex: 0 0 60%;
				}
			}
		}

		&-text {
			margin-left: rem(70);

			@include min540 {
				margin-left: 0;
			}

			span {
				font-size: rem(12);
			}
		}

		.w-7\/12 {
			padding-left: rem(30);

			@include min540 {
				padding-left: 0;
			}
		}
	}

	&__bottom {
		> h1, > h2, > h3, > h4, > h5, > h6 {
			font-size: rem(24);
			margin-bottom: rem(35);

			@include min {
				margin-bottom: rem(24);
			}
		}

		p {
			margin-bottom: rem(20);

			&:last-of-type {
				margin-bottom: rem(40);
			}
		}
	}
}


.field_quantity {
	// width: rem(87);
	display: flex;
	align-items: center;
	// flex-wrap: wrap;
	justify-content: space-between;

	button {
		width: rem(24);
		height: rem(24);
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: var(--c-gray4);

		transition: all 300ms ease;

		&:hover {
			background-color: var(--c-gray2);
		}

		i {
			font-size: rem(10);
		}
	}

	&-input {
		text-align: center;
		width: rem(40);

		font-weight: 500;
		font-size: rem(18);
	}
}

.button_box {
	height: rem(54);

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 0 rem(22);
	background-color: var(--c-yellow);
	transition: all 300ms ease;

	@include min540 {
		padding: 0 rem(14);
	}

	&:hover {
		background-color: var(--c-yellow2);
	}

	span {
		font-size: rem(16);
		font-weight: 600;
		color: white;

		@include min540 {
			font-size: rem(14);
		}
	}

	i {
		font-size: rem(18);
		color: white;
		margin-left: rem(41);

		&::before {
			color: white;
		}
	}
}