.green {
  color: var(--c-green);
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.625rem;
  padding-bottom: 2.0625rem;
}

.header_burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 2.625rem;
  height: 1.75rem;
  cursor: pointer;
}

.header_burger span {
  border-radius: 0.125rem;
  height: 0.1875rem;
  width: 100%;
  background-color: var(--c-yellow);
  transition: all 150ms ease;
}

.header_burger::before, .header_burger::after {
  content: "";
  height: 0.1875rem;
  width: 100%;
  border-radius: 0.125rem;
  background-color: var(--c-yellow);
  transition: all 150ms ease;
}

@media (max-width: 899px) {
  .header_burger {
    display: flex;
  }
}

.header_burger.active {
  position: relative;
}

.header_burger.active span {
  display: none;
}

.header_burger.active::before, .header_burger.active::after {
  position: absolute;
  top: 0.625rem;
  left: 0;
}

.header_burger.active::before {
  transform: rotate(45deg);
}

.header_burger.active::after {
  transform: rotate(-45deg);
}

@media (max-width: 899px) {
  .header {
    padding-top: 1rem;
    padding-bottom: 1.125rem;
    position: sticky;
    top: 0;
    z-index: 15;
    background-color: white;
  }
}

.header__left {
  margin-right: 1.8125rem;
}

@media (max-width: 540px) {
  .header__left {
    margin-right: 0;
    margin-bottom: 1.25rem;
  }
}

.header__left .logo {
  display: block;
}

@media (max-width: 899px) {
  .header__left {
    margin-right: 0;
  }
  .header__left .logo {
    max-width: 11.125rem;
  }
}

@media (max-width: 899px) {
  .header__center {
    order: 9999;
  }
}

@media (max-width: 540px) {
  .header__center {
    order: unset;
    margin-bottom: 1.25rem;
  }
}

@media (max-width: 899px) {
  .header__center nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 45%;
    height: 100%;
    background-color: white;
    z-index: 100;
    padding: 1.25rem;
    box-shadow: 0 0.3125rem 0.625rem rgba(0, 0, 0, 0.2);
    transform: translateX(-100%);
    transition: all 300ms ease;
  }
}

@media (max-width: 540px) {
  .header__center nav {
    width: 70%;
  }
}

.header__center nav ul {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 899px) {
  .header__center nav ul {
    flex-direction: column;
  }
}

.header__center nav ul li {
  margin-right: 1.625rem;
}

@media (max-width: 899px) {
  .header__center nav ul li {
    margin-right: 0;
    margin-bottom: 1.125rem;
  }
}

.header__center nav ul li:last-child {
  margin-right: 0;
}

@media (max-width: 899px) {
  .header__center nav ul li:last-child {
    margin-bottom: 0;
  }
}

.header__center nav ul li a {
  line-height: 1.5rem;
  text-transform: uppercase;
  transition: all 300ms ease;
}

@media (max-width: 899px) {
  .header__center nav ul li a {
    font-size: 1.0625rem;
  }
}

.header__center nav ul li a:hover {
  color: var(--c-yellow);
}

.header__center nav.active {
  transform: translateX(0);
}

.header__right {
  display: flex;
  flex-wrap: wrap;
  margin-right: 1.25rem;
}

@media (max-width: 899px) {
  .header__right {
    margin-left: auto;
  }
}

@media (max-width: 540px) {
  .header__right {
    margin-right: 0;
    margin-left: 0;
    width: 100%;
    justify-content: space-between;
  }
}

.header__right .icon_block {
  margin-right: 2.3125rem;
}

@media (max-width: 899px) {
  .header__right .icon_block {
    margin-right: 1.5rem;
  }
}

@media (max-width: 380px) {
  .header__right .icon_block {
    margin-right: 0;
    width: 100%;
  }
}

.header__right .icon_block:last-child {
  margin-right: 0;
}

@media (max-width: 380px) {
  .header__right .icon_block:last-child {
    margin-top: 0.875rem;
  }
}

.aside {
  padding-right: 0.9375rem;
}

@media (max-width: 899px) {
  .aside {
    padding-right: 0;
    margin-bottom: 1.25rem;
    display: flex;
    flex-wrap: wrap;
  }
}

@media (max-width: 540px) {
  .aside {
    justify-content: space-between;
  }
}

.aside__nav_active {
  display: none;
}

@media (max-width: 899px) {
  .aside__nav_active {
    display: flex;
    align-items: center;
    height: 3.375rem;
    background-color: var(--c-yellow);
    color: white;
    margin-left: 1.25rem;
    padding: 0 0.875rem;
    flex: 0 0 12%;
    transition: all 150ms ease;
  }
  .aside__nav_active span {
    transition: all 150ms ease;
  }
}

@media (max-width: 540px) {
  .aside__nav_active {
    margin-left: 0.625rem;
    flex: 0 0 auto;
    padding: 0;
    flex-grow: 1;
    flex-shrink: 0;
    justify-content: center;
  }
  .aside__nav_active span {
    font-size: 0.75rem;
    padding: 0 0.625rem;
  }
}

@media (max-width: 360px) {
  .aside__nav_active {
    flex: 0 0 100%;
    margin-left: 0;
  }
  .aside__nav_active span {
    font-size: 1rem;
  }
}

@media (max-width: 899px) {
  .aside__nav_active.active {
    position: relative;
  }
  .aside__nav_active.active span {
    opacity: 0;
  }
  .aside__nav_active.active::before {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -0.5625rem;
    margin-left: -0.5625rem;
    content: "\e90c";
    font-family: 'icons';
    color: white;
    font-size: 1.125rem;
    transition: all 150ms ease;
  }
}

.aside__nav {
  margin-top: 2.0625rem;
}

@media (max-width: 899px) {
  .aside__nav {
    width: 100%;
    display: none;
  }
}

.aside__nav > ul > li {
  cursor: pointer;
  border-bottom: 0.0625rem solid var(--c-gray);
}

.aside__nav > ul > li:first-child {
  border-top: 0.0625rem solid var(--c-gray);
}

.aside__nav > ul > li:not(.active):hover span {
  color: var(--c-yellow);
  text-shadow: 0 0 0.04063rem var(--c-yellow), 0 0 0.04063rem var(--c-yellow);
}

.aside__nav > ul > li:not(.active):hover span i::before {
  color: var(--c-yellow);
}

.aside__nav > ul > li ul {
  display: none;
  margin-left: 1.8125rem;
  margin-top: 1.125rem;
  margin-bottom: 2rem;
}

.aside__nav > ul > li ul li {
  margin-bottom: 1.375rem;
}

.aside__nav > ul > li ul li:last-child {
  margin-bottom: 0;
}

.aside__nav > ul > li ul li a {
  line-height: 0.875rem;
  transition: all 100ms ease;
}

.aside__nav > ul > li ul li a:hover {
  color: var(--c-yellow);
  text-shadow: 0 0 0.04063rem var(--c-yellow), 0 0 0.04063rem var(--c-yellow);
}

.aside__nav > ul > li > span {
  color: black;
  text-transform: uppercase;
  line-height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 300ms ease;
}

@media (max-width: 340px) {
  .aside__nav > ul > li > span {
    font-size: 0.75rem;
  }
}

.aside__nav > ul > li > span i {
  transition: all 300ms ease;
  font-size: 0.625rem;
}

.aside__nav > ul > li > span i::before {
  color: var(--c-gray2);
}

.aside__nav > ul > li.active span {
  border-bottom: 0.0625rem solid var(--c-gray);
  transition: none;
  color: var(--c-yellow);
  text-shadow: 0 0 0.04063rem var(--c-yellow), 0 0 0.04063rem var(--c-yellow);
}

.aside__nav > ul > li.active span i::before {
  color: var(--c-yellow);
}

.aside__nav > ul > li.active > ul {
  display: block;
}

.aside__nav > ul > li.active span i {
  transform: rotate(90deg);
}

@media (max-width: 899px) {
  .aside__nav.active {
    display: block;
  }
}

.search {
  position: relative;
}

@media (max-width: 899px) {
  .search {
    flex: 0 0 85%;
  }
}

@media (max-width: 540px) {
  .search {
    flex: 0 0 75%;
