}

@media (max-width: 380px) {
  .search {
    flex: 0 0 100%;
    margin-bottom: 1.25rem;
  }
}

.search input {
  border: 0.0625rem solid var(--c-gray);
  height: 3.375rem;
  width: 100%;
  padding: 0 3.125rem 0 1.25rem;
  font-size: 0.8125rem;
  color: black;
}

.search input:focus {
  border-color: var(--c-gray2);
}

.search input::-webkit-input-placeholder {
  color: black;
}

.search input:-moz-placeholder {
  color: black;
}

.search input::-moz-placeholder {
  color: black;
}

.search input:-ms-input-placeholder {
  color: black;
}

.search__button {
  position: absolute;
  top: 0.9375rem;
  right: 0.75rem;
}

.search__button:hover i {
  color: var(--c-yellow);
}

.search__button:hover i::before {
  color: var(--c-yellow);
}

.search__button i {
  font-size: 1.5rem;
}

.search__button i::before {
  transition: all 300ms ease;
}

.search .live_search {
  position: absolute;
  z-index: 10;
  left: 0;
  top: 3.3125rem;
  width: 100%;
