.content {
	margin-bottom: rem(40);
	&__group {
		margin-top: rem(45);
		display: flex;
		flex-wrap: wrap;
		// justify-content: space-between;

		@include min540 {
			margin-top: rem(24);
		}

		img {
			width: calc((100% - 60px) / 3);
			margin-right: rem(30);
			margin-bottom: rem(30);

			@include min540 {
				width: 100%;
				margin-bottom: rem(15);
				margin-right: 0;

			}

			&:nth-child(3n) {
				margin-right: 0;
			}

		}
	}

	h1 {
		font-size: rem(36);
		font-weight: 600;
		margin-bottom: rem(26);

		@include min {
			font-size: rem(28);
			margin-bottom: rem(20);
		}
	}

	p {
		margin-bottom: rem(20);

		&:last-of-type {
			margin-bottom: 0;
		}
	}

}

p {
	line-height: rem(18);
}

.page-title {
	font-weight: 600;
	font-size: rem(36);
	display: block;
	margin-bottom: rem(32);

	@include min540 {
		font-size: rem(24);
		margin-bottom: rem(22);
	}
}

.card_items {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(rem(200), 1fr));
	gap: rem(20);
	// display: flex;
	// flex-wrap: wrap;
	margin-bottom: rem(70);

	.card {
		// width: calc((100% - 30px) / 4);
	}
}

.page {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	@include min540 {
		justify-content: center;
	}

	&__prev, &__next {
		width: rem(24);
		height: rem(24);
		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(16);
		}
	}

	ul {
		margin: 0 rem(28);

		display: flex;
		flex-wrap: wrap;

		li {
			margin-right: rem(12);
			&:last-child {
				margin-right: 0;
			}

			a {

				width: rem(32);
				height: rem(32);
				border-radius: 50%;
				color: black;
				display: flex;
				align-items: center;
				justify-content: center;
				transition: all 100ms ease;

				&:hover {
					color: white;
					background-color: var(--c-yellow2);
				}
			}

			a.active {
				color: white;
				background-color: var(--c-yellow2);
			}
		}
	}
}

.tab {
	margin-bottom: rem(70);
	
	&__item {
		display: none;
	}

	&__item.active {
		

		display: block;
	}


}

.card {
	background-color: white;
	border: rem(1) solid var(--c-gray2);
	padding: rem(20) rem(18);

	display: flex;
	flex-direction: column;

	&__top {
		margin-bottom: rem(20);
		height: rem(132);
		min-height: rem(132);

		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__bottom {
		display: flex;
		flex-direction: column;
		height: 100%;
		.title, .subtitle {
			display: block;
		}

		.title {
			font-weight: 500;
			line-height: rem(18);
			margin-bottom: rem(14);
			font-size: rem(14);
			transition: all 150ms ease;

			&:hover {
				color: var(--c-yellow);
			}
		}

		.subtitle {
			font-size: rem(12);
			line-height: rem(24);
			margin-bottom: rem(6);
			margin-top: auto;
		}

		.price {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;

			span {
				font-weight: 500;
			}

			&__link {
				color: var(--c-yellow);
				display: flex;
				align-items: center;
				font-weight: 500;
				transition: all 150ms ease;

				&:hover {
					text-shadow: 0 0 rem(0.65) var(--c-yellow),
					0 0 rem(0.65) var(--c-yellow);
				}

				&::after {
					content: "\e901";
					font-family: 'icons';
					color: var(--c-yellow);
					margin-left: rem(7);
				}
			}
		}
	}
}