.table {
	margin-bottom: rem(18);
	border-bottom: rem(2) solid var(--c-yellow);

	&__row {
		padding: 0 rem(15);
		height: rem(58);
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		background-color: white;

		@include min540 {
			height: auto;
			flex-direction: column;
			padding: rem(15) rem(15) rem(74) rem(15);

			&:first-child {
				display: none;
			}
		}

		&:nth-child(2n) {
			background-color: var(--c-gray5);
		}

		> div {
			display: flex;
			@include min540 {
				position: relative;
				justify-content: space-between;
				margin-bottom: rem(20);
				border-bottom: rem(1) dashed var(--c-gray3);
				padding-bottom: rem(5);

				&:last-child {
					margin-bottom: 0;
					padding-bottom: 0;
					border-bottom: 0;
				}
			}
			
			&:first-child {
				// width: rem(340);
				flex: 0 0 rem(340);

				@include min {
					flex: 0 0 40%;
				}

				@include min540 {
					flex: 0 0 100%;
					&::before {
						content: attr(data-name);
						font-weight: 600;
						flex: 0 0 50%;
					}

					span {
						text-align: right;
					}
				}
			}
			&:nth-child(2) {
				flex: 0 0 rem(160);

				@include min {
					flex: 0 0 20%;
				}

				@include min540 {
					flex: 0 0 100%;
					width: 100%;
					&::before {
						content: attr(data-name);
						font-weight: 600;
						flex: 0 0 50%;
					}
				}
			}
			&:nth-child(3) {
				flex: 0 0 rem(168);

				@include min {
					flex: 0 0 20%;
				}

				@include min540 {
					flex: 0 0 100%;
					width: 100%;

					&::before {
						content: attr(data-name);
						font-weight: 600;
						flex: 0 0 50%;
					}
				}
			}
			&:last-child {
				flex-grow: 1;

				@include min540 {
					flex: 0 0 100%;
					width: 100%;

					&::before {
						content: attr(data-name);
						font-weight: 600;
						flex: 0 0 50%;
					}
				}
			}

			span {
				@include min540 {

				}
			}

			.field_quantity {
				max-width: rem(87);
			}

			b {
				font-weight: 600;
				font-size: rem(16);
			}

			.cart_item_delete {
				margin-left: auto;

				@include min540 {
					width: 100%;
					margin-left: 0;

					position: absolute;
					right: 0;
					bottom: rem(-62);

					height: rem(46);
					background-color: var(--c-yellow);
				}

				i {
					font-size: rem(16);

					@include min540 {
						// position: relative;
						// z-index: 1;
						// color: white;
						font-size: rem(20);

						&::before {
							color: white;
						}
					}
				}
			}
		}
	}
}

.table_result {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	@include min540 {
		justify-content: center;
	}

	.button_default {
		height: auto;

		@include min540 {
			margin-bottom: rem(12);
		}
	}

	b {
		font-weight: 600;
	}
}

.form_message {
	margin-top: rem(50);
	margin-bottom: rem(70);

	display: flex;
	flex-wrap: wrap;

	background-color: var(--c-gray5);
	padding: rem(50);

	@include min540 {
		padding: rem(20);
		margin-top: rem(30);
		margin-bottom: rem(50);
	}

	&__left, &__right {
		flex: 0 0 50%;

		@include min540 {
			flex: 0 0 100%;
		}
	}
	&__left {
		padding-right: rem(18);

		@include min540 {
			padding-right: 0;
			margin-bottom: rem(20);
		}
	}
	&__right {
		padding-left: rem(18);
		display: flex;
		flex-direction: column;

		@include min540 {
			padding-left: 0;
		}

		textarea.field {
			height: rem(256);
			resize: none;
		}

		.button_box {
			margin-top: auto;
		}
	}
}

.field_set {
	margin-bottom: rem(18);
	&:last-child {
		margin-bottom: 0;
	}
	label {
		font-size: rem(16);
		display: flex;
		align-items: flex-start;
		margin-bottom: rem(7);

		span {
			margin-left: rem(2);
			color: var(--c-yellow);
		}
	}

	.captcha {
		display: flex;
		// flex-wrap: wrap;

		&__figure {
			flex: 0 0 rem(200);
			height: rem(54);
			margin-right: rem(15);
		}

		.field {
			// flex: 0 0 auto;
		}
	}
}