.footer {
	// margin-top: rem(145);

	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;
	padding-top: rem(70);
	padding-bottom: rem(65);

	@include min {
		flex-wrap: wrap;
		padding-top: rem(40);
		padding-bottom: rem(35);
	}

	&__left {
		margin-right: rem(154);
		max-width: rem(370);

		@include min {
			max-width: 100%;
			margin-right: 0;

			display: flex;
			justify-content: space-between;
			// flex-wrap: wrap;
		}

		@include min540 {
			flex-wrap: wrap;
			margin-bottom: rem(35);
		}

		.logo {
			width: rem(167);
			display: block;
			margin-bottom: rem(32);

			@include min {
				max-width: 50%;
			}

			@include min540 {
				max-width: 100%;
				width: 100%;
				display: flex;
				justify-content: center;

				img {
					width: rem(167);
				}
			}
		}

		.text {
			font-size: rem(12);

			@include min {
				width: 50%;
			}

			@include min540 {
				width: 100%;
			}
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		// flex-grow: 1;
		justify-content: space-between;

		max-width: rem(645);
		width: 100%;

		@include min {
			max-width: 100%;
		}

		&-row {
			display: flex;
			// flex-wrap: wrap;
			flex-grow: 1;

			@include min540 {
				flex-wrap: wrap;
			}

			.last {
				justify-content: flex-end;

				@include min540 {
					justify-content: center;
				}
			}

			> div {
				
				width: 50%;

				@include min540 {
					width: 100%;
					justify-content: center;
				}

				&:first-child {
					// width: rem(225);
					// margin-right: rem(164);
				}
			}

			&:first-child {
				justify-content: flex-end;

				@include min540 {
					justify-content: center;
				}
			}

			&:last-child {
				>div {
					width: 100%;
					&:first-child {
						padding-left: rem(42);

						@include min540 {
							padding-left: 0;
						}
					}
				}
			}
		}

		// > div {
		// 	width: calc(100% / 2);
		// }

		.icon_block {
			margin-bottom: rem(35);
			&:nth-child(2) {
				justify-content: flex-end;

				@include min540 {
					justify-content: center;
				}
			}
		}

		&-item {
			font-size: rem(12);
			@include min540 {
				text-align: center;
			}
			&:last-child {
				justify-content: flex-end;
				text-align: right;

				@include min540 {
					justify-content: center;
					text-align: center;
					margin-top: rem(24);
				}
			}
		}
	}
}