:root {
	--c-black-3: rgba(0,0,0,.3);
	--c-yellow: #F6A713;
	--c-yellow2: #FFA100;
	--c-yellow3: #FF7A00;
	--c-gray: #D3D3D3;
	--c-gray2: #C4C4C4;
	--c-gray3: #DDDDDD;
	--c-gray4: #F1F1F1;
	--c-gray5: #F7F7F9;
	--c-gray6: #E5E5E5;

	--c-green: #37C03D;
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	height: 100%;

	@include w1200_w900 {
		font-size: 78%;
	}
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-size: rem(14);
	font-family: 'Montserrat', sans-serif;
	font-weight: normal;
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.container {
	width: 1170px;
	margin: 0 auto;

	@include w1200_w900 {
		width: calc(100% - 40px);
	}

	@include min {
		width: calc(100% - 40px);
	}
}

.relative {
	position: relative;
}

.w-9\/12 {
	flex: 0 0 75%;
	max-width: 75%;
	padding-left: rem(15);

	@include min {
		flex: 0 0 100%;
		max-width: 100%;
		padding-left: 0;
	}
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;

	@include min540 {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;

	@include min540 {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-3\/12 {
	flex: 0 0 25%;
	max-width: 25%;

	@include min {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.icon_block {
	display: flex;
	flex-wrap: wrap;

	&__image {
		margin-right: rem(11);

		i {
			font-size: rem(31);

			@include min {
				font-size: rem(24);
			}
		}
	}

	&__text {
		display: flex;
		flex-direction: column;
		
		height: rem(46);
		justify-content: center;

		@include min {
			height: rem(36);
		}

		a, > span, > b {
			display: block;
		}

		.tel {
			font-size: rem(24);
			line-height: rem(20);

			@include min {
				font-size: rem(22);
				line-height: rem(18);
			}
		}

		span {
			font-size: rem(11);
			line-height: rem(24);
			letter-spacing: rem(0.2);
		}

		b {
			font-size: rem(14);
			font-weight: normal;
			line-height: rem(14);
			margin-top: rem(5);

			@include min {
				font-size: rem(13);
				line-height: rem(13);
			}
		}
	}
}

.green {
	color: var(--c-green);
}