.table_item {
	margin-bottom: rem(60);

	@include min {
		margin-bottom: rem(30);
	}

	&__item {
		width: 100%;
		background-color: white;
		padding: rem(10);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		border-bottom: rem(1) solid var(--c-gray2);

		transition: all 150ms ease;

		&:first-child {
			border-top: rem(1) solid var(--c-gray2);
		}

		&:hover {
			background-color: var(--c-gray5);
		}

		div {

			.image {
				width: rem(42);
				height: rem(42);
				border: rem(1) solid var(--c-gray2);
				
				display: flex;
				align-items: center;
				justify-content: center;
			}

			p {
				max-width: rem(300);

				@include min {
					max-width: 100%;
				}
			}

			&:first-child {
				flex: 0 0 rem(70);

				@include min {
					flex: 0 0 10%;
				}

				@include min540 {
					margin-right: rem(14);
					// flex: 0 0 30%;
				}
			}

			&:nth-child(2) {
				flex: 0 0 rem(187);

				@include min {
					flex: 0 0 20%;
				}

				@include min540 {
					flex: 0 0 35%;
				}
			}

			&:nth-child(3) {
				flex: 0 0 rem(200);

				@include min {
					flex: 0 0 20%;
				}

				@include min540 {
					text-align: right;
					flex: 0 0 35%;
				}
			}

			&:last-child {
				flex-grow: 1;
				// flex-shrink: 0;

				@include min {
					flex-grow: 0;
					flex: 0 0 50%;
				}

				@include min540 {
					flex: 0 0 100%;
					margin-top: rem(12);
				}
			}
		}
	}
}