.header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	padding-top: rem(26);
	padding-bottom: rem(33);
	

	&_burger {
		display: none;
		flex-direction: column;
		justify-content: space-between;
		width: rem(42);
		height: rem(28);
		cursor: pointer;


		span {
			border-radius: rem(2);
			height: rem(3);
			width: 100%;
			background-color: var(--c-yellow);
			transition: all 150ms ease;
		}

		&::before,
		&::after {
			content: "";
			height: rem(3);
			width: 100%;
			border-radius: rem(2);
			background-color: var(--c-yellow);
			transition: all 150ms ease;
		}

		@include min {
			display: flex;
		}
	}

	&_burger.active {
		position: relative;
		span {
			display: none;
		}

		&::before,
		&::after {
			position: absolute;
			top: rem(10);
			left: 0;
		}

		&::before {
			transform: rotate(45deg);
		}
		&::after {
			transform: rotate(-45deg);
		}
	}

	@include min {
		padding-top: rem(16);
		padding-bottom: rem(18);

		position: sticky;
		top: 0;
		z-index: 15;
		background-color: white;
	}

	&__left {
		margin-right: rem(29);

		@include min540 {
			margin-right: 0;
			margin-bottom: rem(20);
		}

		.logo {
			display: block;
		}

		@include min {
			margin-right: 0;

			.logo {
				max-width: rem(178);
			}
		}
	}

	&__center {
		@include min {
			order: 9999;
		}
		@include min540 {
			order: unset;
			margin-bottom: rem(20);
		}
		nav {
			@include min {
				position: fixed;
				top: 0;
				left: 0;
				width: 45%;
				height: 100%;
				background-color: white;
				z-index: 100;
				padding: rem(20);
				box-shadow: 0 rem(5) rem(10) rgba(0,0,0,.2);
				transform: translateX(-100%);
				transition: all 300ms ease;
			}
			@include min540 {
				width: 70%;
			}
			ul {

				display: flex;
				flex-wrap: wrap;

				@include min {
					flex-direction: column;
				}

				li {
					margin-right: rem(26);

					@include min {
						margin-right: 0;
						margin-bottom: rem(18);
					}

					&:last-child {
						margin-right: 0;

						@include min {
							margin-bottom: 0;
						}
					}
					a {

						line-height: rem(24);
						text-transform: uppercase;
						transition: all 300ms ease;

						@include min {
							font-size: rem(17);
						}

						&:hover {
							color: var(--c-yellow);
						}
					}
				}
			}
		}

		nav.active {
			transform: translateX(0);
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;
		margin-right: rem(20);

		@include min {
			// margin-right: 0;
			margin-left: auto;
		}

		@include min540 {
			margin-right: 0;
			margin-left: 0;
			width: 100%;
			justify-content: space-between;
		}

		.icon_block {
			margin-right: rem(37);

			@include min {
				margin-right: rem(24);
			}

			@include min380 {
				margin-right: 0;
				width: 100%;
			}

			&:last-child {
				margin-right: 0;

				@include min380 {
					margin-top: rem(14);
				}
			}
		}
	}
}