.aside {
	padding-right: rem(15);

	@include min {
		padding-right: 0;
		margin-bottom: rem(20);

		display: flex;
		flex-wrap: wrap;
	}

	@include min540 {
		justify-content: space-between;
	}

	&__nav_active {
		display: none;

		@include min {
			display: flex;
			align-items: center;
			height: rem(54);
			background-color: var(--c-yellow);
			color: white;
			// border: rem(1) solid var(--c-gray);
			margin-left: rem(20);
			padding: 0 rem(14);
			flex: 0 0 12%;

			transition: all 150ms ease;

			span {
				transition: all 150ms ease;

			}
		}

		@include min540 {
			margin-left: rem(10);
			flex: 0 0 auto;
			padding: 0;
			flex-grow: 1;
			flex-shrink: 0;
			justify-content: center;
			span {
				font-size: rem(12);
				padding: 0 rem(10);
			}
		}

		@include min360 {
			flex: 0 0 100%;
			margin-left: 0;

			span {
				font-size: rem(16);
			}
		}
	}
	&__nav_active.active {
		@include min {
			position: relative;

			span {
				opacity: 0;
			}

			&::before {
				position: absolute;
				top: 50%;
				left: 50%;
				margin-top: rem(-9);
				margin-left: rem(-9);
				content: "\e90c";
				font-family: 'icons';
				color: white;
				font-size: rem(18);
				transition: all 150ms ease;
			}
		}
	}

	&__nav {
		// display: block;
		margin-top: rem(33);

		@include min {
			// position: fixed;
			// top: 0;
			// left: 0;
			// height: 100%;
			// // display: none;
			// width: 75%;
			// background-color: white;
			// z-index: 101;
			// margin-top: 0;
			// padding: rem(20);
			width: 100%;
			display: none;
		}

		> ul {

			> li {
				cursor: pointer;
				// border-top: rem(1) solid var(--c-gray);
				border-bottom: rem(1) solid var(--c-gray);

				&:first-child {
					border-top: rem(1) solid var(--c-gray);
				}

				&:not(.active):hover {

					// border-color: var(--c-yellow);

					span {
						color: var(--c-yellow);
						text-shadow: 0 0 rem(0.65) var(--c-yellow),
						0 0 rem(0.65) var(--c-yellow);

						i {
							&::before {
								color: var(--c-yellow);
							}
						}
					}
				}

				ul {
					display: none;
					margin-left: rem(29);
					margin-top: rem(18);
					margin-bottom: rem(32);

					li {
						margin-bottom: rem(22);

						&:last-child {
							margin-bottom: 0;
						}

						a {
							line-height: rem(14);
							transition: all 100ms ease;

							&:hover {
								color: var(--c-yellow);
								text-shadow: 0 0 rem(0.65) var(--c-yellow),
								0 0 rem(0.65) var(--c-yellow);
							}
						}
					}
				}

				>span {
					color: black;
					text-transform: uppercase;
					// font-size: rem(14);
					line-height: rem(48);

					display: flex;
					align-items: center;
					// flex-wrap: wrap;
					justify-content: space-between;

					transition: all 300ms ease;

					@include min340 {
						font-size: rem(12);
					}

					i {
						transition: all 300ms ease;
						font-size: rem(10);
						&::before {
							color: var(--c-gray2);
						}
					}
				}
			}

			> li.active {

				span {
					border-bottom: rem(1) solid var(--c-gray);
					transition: none;

					color: var(--c-yellow);
					text-shadow: 0 0 rem(0.65) var(--c-yellow),
					0 0 rem(0.65) var(--c-yellow);

					i {
						&::before {
							color: var(--c-yellow);
						}
					}
				}

				> ul {
					display: block;
				}
				span {
					i {
						transform: rotate(90deg);
					}
				}
			}
		}
	}
	&__nav.active {
		@include min {
			display: block;
		}
	}
}

.search {
	position: relative;
	
	@include min {
		// width: 85%;
		flex: 0 0 85%;
	}

	@include min540 {
		flex: 0 0 75%;
	}

	@include min380 {
		flex: 0 0 100%;
		margin-bottom: rem(20);
	}

	input {
		border: rem(1) solid var(--c-gray);
		height: rem(54);
		width: 100%;
		padding: 0 rem(50) 0 rem(20);
		font-size: rem(13);
		color: black;

		&:focus {
			border-color: var(--c-gray2);
		}

		@include placeholder {
			color: black;
		}
	}

	&__button {
		position: absolute;
		top: rem(15);
		right: rem(12);

		&:hover {
			i {
				color: var(--c-yellow);
				&::before {
					color: var(--c-yellow);
				}
			}
		}

		i {
			
			font-size: rem(24);

			&::before {
				transition: all 300ms ease;
			}
		}
	}

	.live_search {
		position: absolute;
		z-index: 10;
		left: 0;
		top: rem(53);
		width: 100%;
		background-color: white;
		border: rem(1) solid var(--c-gray);
		box-shadow: 0 0 rem(10) rgba(0, 0, 0, 0.1);

		display: none;

		// max-height: rem(480);

		&__item {
			display: flex;
			// flex-wrap: wrap;
			align-items: center;
			height: rem(62);
			padding: rem(10);
			border-bottom: rem(1) solid var(--c-gray);

			&:last-child {
				border-bottom: 0;
			}

			&:hover {
				background-color: var(--c-gray5);
			}
			
			.image {
				width: rem(42);
				height: rem(42);
				border: rem(1) solid var(--c-gray2);
				margin-right: rem(14);

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.text {
				font-size: rem(10);
				line-height: rem(14);
				letter-spacing: rem(0.2);
			}
		}
	}
	.live_search.active {
		display: block;
	}
}