.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.2);

	// display: flex;
	align-items: center;
	justify-content: center;

	display: none;

	z-index: 100;

	&_close {
		position: absolute;
		top: 0;
		right: 0;
		width: rem(50);
		height: rem(50);
		background-color: #F0F0F0;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__wrap {
		position: relative;
		width: rem(690);
		background-color: white;
		padding: rem(50);

		&-header {
			margin-bottom: rem(30);
			.title {
				font-size: rem(24);
				font-weight: 600;
			}
		}

		&-content {
			margin-bottom: rem(58);
		}

		&-item {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			padding-bottom: rem(18);
			border-bottom: rem(1) solid var(--c-gray2);
			margin-bottom: rem(18);

			&:last-child {
				margin-bottom: 0;
			}

			.left {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				.image {
					width: rem(80);
					height: rem(80);
					display: flex;
					align-items: center;
					justify-content: center;
					border: rem(1) solid var(--c-gray2);
					margin-right: rem(15);
				}

				span {
					font-size: rem(12);
					letter-spacing: rem(0.2);

					// flex-grow: 1;
					width: rem(213);
				}
			}

			.right {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				.field_quantity {
					margin-right: rem(40);
				}
				.price {
					font-size: rem(12);
				}
			}
		}

		&-result {
			display: flex;
			justify-content: flex-end;

			span {
				font-size: rem(12);
			}
		}

		&-button {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;
			align-items: center;

			.button_default {
				margin-right: rem(32);
			}

			.button_box {
				padding: 0 rem(18);
				i {
					margin-left: rem(15);
				}
			}
		}
	}
}

.popup.active {
	display: flex;

}

.button_default {
	height: rem(54);

	display: flex;
	align-items: center;

	span {
		font-size: rem(16);
		font-weight: 600;
		color: var(--c-yellow);
		margin-right: rem(11);
	}

	i {
		font-size: rem(18);
	}
}