.map {
	height: rem(424);
	margin-bottom: rem(50);

	@include min540 {
		margin-bottom: rem(30);
		height: auto;
	}
}

.table_item {
	display: flex;
	flex-wrap: wrap;
	// justify-content: space-between;

	.icon_block_col {
		width: calc(100% / 3);

		@include min540 {
			width: 100%;
			margin-bottom: rem(20);

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

.icon_block_col {
	display: flex;
	flex-direction: column;
	align-items: center;

	i {
		font-size: rem(31);
		margin-bottom: rem(16);
	}

	.title {
		margin-bottom: rem(2);
		font-size: rem(11);
		line-height: rem(24);
	}

	.last {
		font-size: rem(18);
		line-height: rem(24);
		color: black;
		text-align: center;
		max-width: rem(260);
	}
}