.question {
	background-color: var(--c-gray5);
	margin-top: rem(76);

	padding-top: rem(59);
	padding-bottom: rem(66);

	@include min540 {
		padding-top: rem(30);
		padding-bottom: rem(35);
	}

	&__header {
		display: flex;
		flex-direction: column;
		align-items: center;

		.title {
			margin-bottom: rem(22);
			font-size: rem(36);
			font-weight: 600;

			@include min540 {
				font-size: rem(24);
			}
		}

		p {
			margin-bottom: rem(28);
		}
	}

	&__form {

		&-col {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin-bottom: rem(36);

			@include min540 {
				margin-bottom: 0;
			}

			.field {
				width: calc((100% - 2.5rem) / 3);

				@include min540 {
					width: 100%;
					margin-bottom: rem(24);
				}
			}
		}

		&-text {
			margin-bottom: rem(24);
			textarea.field {
				min-height: rem(106);
				resize: none;
			}
		}

		&-bottom {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			.button_gray {
				position: relative;
				cursor: pointer;
				margin-right: rem(29);
				margin-left: auto;

				@include min540 {
					flex: 0 0 100%;
					margin-right: 0;
					margin-left: 0;
					margin-bottom: rem(28);
					justify-content: center;
				}

				input {
					display: none;
				}

				.field_attachment-name {
					position: absolute;
					bottom: rem(-26);
					left: 0;
					font-size: rem(12);

					@include min540 {
						bottom: rem(-23);
					}
				}
			}

			@include min540 {
				.button_box {
					flex: 0 0 100%;
					justify-content: center;
				}
			}

			@include min {
				.field_checked {
					width: 100%;
					margin-bottom: rem(20);

					p {
						max-width: 90%;
					}
				}
			}
		}
	}
}

textarea.field {
	padding: rem(20);
}

.field {
	border: rem(1) solid var(--c-gray6);
	height: rem(56);
	width: 100%;
	background-color: white;
	padding: 0 rem(20);
	font-size: rem(16);

	&:focus {
		border-color: var(--c-gray2);
	}

	@include placeholder {
		color: black;
	}
}

.field_checked {
	cursor: pointer;

	display: flex;
	flex-wrap: wrap;

	p {
		font-size: rem(12);
		margin-left: rem(10);
		max-width: rem(513);
	}

	input {
		display: none;

		&:checked {
			~ .check {
				&::before {
					opacity: 1;
				}
			}
		}
	}

	.check {
		position: relative;
		width: rem(18);
		height: rem(18);
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #F3F3F3;

		&::before {
			content: "\e90b";
			position: absolute;
			z-index: 1;
			font-family: 'icons';
			top: rem(2);
			left: rem(4);

			font-size: rem(12);
			color: var(--c-yellow3);
			transition: all 150ms ease;
			opacity: 0;

		}
	}
}

.button_gray {
	background-color: #F3F3F3;
	border: rem(1) solid var(--c-gray6);
	height: rem(54);
	padding: 0 rem(28);

	display: flex;
	align-items: center;
	flex-wrap: wrap;

	transition: all 150ms ease;

	@include min540 {
		padding: 0 rem(14);
	}

	&:hover {
		background-color: var(--c-gray6);
	}

	i {
		font-size: rem(18);
		margin-right: rem(16);
	}
	span {
		font-size: rem(16);

		@include min540 {
			font-size: rem(14);
		}
	}
}